@import url('https://fonts.googleapis.com/css2?family=Fraunces:opsz,wght@9..144,300;400;600;700&family=Plus+Jakarta+Sans:wght@400;500;600;700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
    --brand-primary: #007BA7;
    --brand-primary-hover: #006285;
    --brand-primary-light: #E0F2F7;
    --brand-background: #FFFBF7;
    --brand-paper: #FFFFFF;
    --brand-text-primary: #1A202C;
    --brand-text-secondary: #4A5568;
    --brand-text-muted: #718096;
    --brand-accent: #F6AD55;
    --brand-accent-soft: #FEEBC8;
    --brand-success: #48BB78;
    --brand-error: #F56565;
}

body {
    margin: 0;
    font-family: 'Plus Jakarta Sans', -apple-system, BlinkMacSystemFont, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: var(--brand-background);
    color: var(--brand-text-primary);
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Fraunces', Georgia, serif;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

/* Smooth scroll behavior */
html {
    scroll-behavior: smooth;
}

/* Custom scrollbar */
::-webkit-scrollbar {
    width: 8px;
}

::-webkit-scrollbar-track {
    background: var(--brand-background);
}

::-webkit-scrollbar-thumb {
    background: var(--brand-primary);
    border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
    background: var(--brand-primary-hover);
}

/* Selection styling */
::selection {
    background: var(--brand-primary-light);
    color: var(--brand-text-primary);
}

@layer base {
    :root {
        --background: 40 100% 99%;
        --foreground: 220 39% 11%;
        --card: 0 0% 100%;
        --card-foreground: 220 39% 11%;
        --popover: 0 0% 100%;
        --popover-foreground: 220 39% 11%;
        --primary: 195 100% 33%;
        --primary-foreground: 0 0% 100%;
        --secondary: 195 50% 95%;
        --secondary-foreground: 195 100% 33%;
        --muted: 210 20% 96%;
        --muted-foreground: 215 20% 45%;
        --accent: 33 95% 66%;
        --accent-foreground: 220 39% 11%;
        --destructive: 0 72% 65%;
        --destructive-foreground: 0 0% 100%;
        --border: 220 13% 91%;
        --input: 220 13% 91%;
        --ring: 195 100% 33%;
        --radius: 0.75rem;
    }
}

@layer base {
    * {
        @apply border-border;
    }
    body {
        @apply bg-background text-foreground;
    }
}

@layer base {
    [data-debug-wrapper="true"] {
        display: contents !important;
    }
}
